.index-blogs{
 

    padding: 3rem 5rem 0 5rem;

    display: grid;
    grid-template-rows: 20rem 20rem 20rem 20rem 6rem;
    grid-gap: 2rem;

    @include respond(phone) {
        grid-template-rows: 10rem 10rem 10rem 10rem 3rem;
    }


    
    &--button{
        grid-row: 5/6;
        display: grid;
        grid-template-columns: 1fr 120rem 1fr ;
        margin-bottom: 1rem;
        padding: 1rem;


        @include respond(phone) {
            grid-template-columns: 1fr 20rem 1fr ;
            margin-bottom: 1rem;
          
        }

        &__btn1{
            background-color: rgb(217, 216, 216);
            font-size: 1.5rem;
            border: 1px solid black;

            @include respond(phone) {
               font-size: 1rem;
            }
        }

        &__btn2{
            grid-row: 1/2;
            grid-column:3/4;
            background-color: rgb(217, 216, 216);
            font-size: 1.5rem;
  
            border: 1px solid black;

            @include respond(phone) {
                font-size: 1rem;
             }
        }
    }
}













































