.user-blogs {
    background-color: rgb(209, 208, 208);

    padding: 3rem 5rem 0 5rem;

    display: grid;
    grid-template-rows: 5rem 20rem 20rem 20rem 20rem 5rem;
    grid-gap: 2rem;

    @include respond(phone) {
        grid-template-rows: 2rem 10rem 10rem 10rem 10rem 5rem;
    }


    &__addbutton {
        display: grid;
        grid-template-columns: 1fr 20rem 1fr;

        @include respond(phone) {
            grid-template-columns: 1fr 10rem 1fr;
        }

        &--btn {
            grid-column: 2/3;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 1.5rem;
            text-decoration: none;
            color: black;
            background-color: rgb(182, 174, 174);
            border: 1px solid black;

            @include respond(phone) {
                font-size: 1rem;
            }
        }
    }



    &--button {
        grid-row: 6/7;
        display: grid;
        grid-template-columns: 1fr 120rem 1fr;
        padding: 1rem;

        @include respond(phone) {
            grid-template-columns: 1fr 20rem 1fr ;
            margin-bottom: 1rem;
          
        }

        &__btn1{
            background-color: rgb(217, 216, 216);
        }

        &__btn2 {
            grid-row: 1/2;
            grid-column: 3/4;
            background-color: rgb(217, 216, 216);
        }
    }
}