.blog {
   
    padding: 2rem;
    display: grid;
    grid-template-rows: 5rem 5rem 5rem 2px;
    grid-template-columns: 110rem 1fr 1fr;

    @include respond(phone) {
        padding: 0;
        grid-template-rows: 3rem 3rem 3rem  0.5px;
        grid-template-columns: 20rem 1fr 1fr;
    }

    

    &__line{
        grid-row: 4/5;
        grid-column: 1/-1;
        border-top: 1px solid black;
    }

    &__link {
    
        grid-column: 1/-1;
        text-decoration: none;     
        color: black;
        transition: all 0.1s ease;

        &:hover{
            color: #c02d2d;
        }

        &--title {
            grid-row: 1/2;
            grid-column: 1/-1;
            font-weight: 700;
            font-size: 4rem;
            @include respond(phone) {
                font-size: 1.3rem;
            }
        }

        &--subtitle {
            grid-row: 2/3;
            grid-column: 1/-1;
            font-weight: 600;
            margin-top: 1rem;
            font-size: 1.9rem;

            @include respond(phone) {
                margin-top: 0;
                font-size: 1.2rem;
            }
        }
    }

    &__detail {
        font-size: 1.5rem;
        font-weight: 400;
        grid-row: 3/4;
        grid-column: 1/2;
        margin-top: 1.5rem;
        @include respond(phone) {
            margin-top: 0;
            font-size: 1rem;
            grid-column: 1/3;
        }
    }

    &__edit {
        grid-row: 3/4;
        grid-column: 2/3;
        display: flex;
        align-items: center;
        justify-content: center;


        &--btn {
            padding: 0.5rem 1rem;
            text-decoration: none;
            color: black;
            font-size: 1.5rem;

            @include respond(phone) {
                font-size: 1rem;
            }
            

        }


    }

    &__delete {
        grid-row: 3/4;
        grid-column: 3/4;
        display: flex;
        align-items: center;
        justify-content: center;

        &--btn {
            padding: 0.5rem 1rem;
            text-decoration: none;
            color: black;
            font-size: 1.5rem;
            
            @include respond(phone) {
                font-size: 1rem;
            }
        }
    }
}